import './App.css';
import { PaymentPage } from '../src/screens/payment/PaymentPage'
import React, {useEffect} from 'react'


function App() {


  const onMessageHandler = (e) => {

    const event = JSON.parse(e.data)
    window.ReactNativeWebView.postMessage(JSON.stringify({ event: event }))
    if (event.name) {
    }
  }
    

  useEffect(() => {
    console.log("Init Page")
    // const isUIWebView = () => {
    //   return navigator.userAgent
    //     .toLowerCase()
    //     .match(/\(ip.*applewebkit(?!.*(version|crios))/)
    // }
  
    // const receiver = isUIWebView() ? window : document
    const receiver = window
    receiver.addEventListener('message', onMessageHandler)
    return () => {
      receiver.removeEventListener('message', onMessageHandler)
    }
  
    
  },[])

  return (
    <div className="App">
      <header className="App-header">
        <PaymentPage/>
      </header>
    </div>
  );
}

export default App;
